

























































import uniIcon from "@/components/uni-icon/uni-icon.vue"
export default{
	components: {uniIcon},
	data(){
		return {
			subaccount:false,
			bianJi:true,
			sonName:"", //获取用户输入的用户名
			sonPhone:"", //获取用户输入的电话
			sonPwd:"",  //获取用户输入的密码
			Signboard:'',
			Signguid:'',
			subaccountInfo:'', //定义this.subaccountInfo 来存 res.data.subaccount;
			subid: '' ,//用来存items.uid
			inputName: '',
			inputPhone: ''
		}
	},
	methods:{
		// 编辑地址
		composeTap(e){
			this.subaccount = true,
			this.bianJi = false,
			this.subid = e.uid,
			this.inputName = e.uname,
			this.inputPhone = e.telephone
		},
		//获取用户输入的数据 1.用户名 2.电话 3.密码
		name:function(evt){
			this.sonName=evt.target.value;
		},
		phone:function(evt){
			this.sonPhone=evt.target.value;
		},
		password:function(evt){
			this.sonPwd=evt.target.value;
		}, //点击修改页面的确定按钮触发的事件
		upSubaccounts(evt){
			//对表单进行验证
			if(this.sonName==""||this.sonPhone==""||this.sonPwd==""){
				uni.showToast({
					title:'请将信息填写完整,不允许为空',
					icon:'none'
				})
			}else if(!RegExp(/^[\u4E00-\u9FA5A-Za-z0-9]+$/).test(this.sonName)){
				uni.showToast({
					title:"用户名只能包含中文，字母，数字",
					icon: "none"
				})
			}else if(!RegExp(/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9]|17[0|1|2|3|5|6|7|8|9])\d{8}$/).test(this.sonPhone)){
				uni.showToast({
					title:"手机号码格式不正确",
					icon: "none"
				})
			}else if(!RegExp(/^[A-Za-z0-9]{5,16}$/).test(this.sonPwd)){
				uni.showToast({
					title:"密码只能由字母数字组成,长度不能小于6位大于16位",
					icon: "none"
				})
			}else{
				//调updSubAccount(修改子账户)的接口
				uni.request({
					url:'http://192.168.199.194/mobile/Subaccount/updSubAccount',
					method:'POST',
					data:{
						Ident_Signboard: this.Signboard,
						Ident_Signguid: this.Signguid,
						uid: this.subid,
						uname:this.sonName,
						telephone:this.sonPhone,
						password:this.sonPwd
					},
					success: (res) => {
						console.log(res);
						if(res.data.status==0){
							uni.showToast({
								title:res.data.message,
								icon:'none'
							})
							this.bianJi = true;
							this.subaccount = false;
							uni.navigateTo({
								url:'../user/subaccount'
							})
						}else{
							uni.showToast({
								title:'子账号修改失败',
								icon:'none'
							})
						}
					}
				})
			}
		},
		dlSubaccounts(evt){
			this.subid = evt.uid;
			uni.request({
				url:'http://192.168.199.194/mobile/Subaccount/delSubAccount',
				method:'GET',
				data:{
					uid:this.subid,
					Ident_Signboard: this.Signboard,
					Ident_Signguid: this.Signguid
				},
				success: (res) => {
					console.log(res);
					if(res.data.status==0){
						uni.showToast({
							title:res.data.message,
							icon:'none'
						})
						uni.navigateTo({
							url:'../user/subaccount'
						})
					}else{
						uni.showToast({
							title:'子账户删除失败',
							icon:'none'
						})
					}
				}
			})
		}
	},
	onLoad:function(){
		var jsonList=uni.getStorageSync("jsonList");
		var data = JSON.parse(jsonList); //JSON字符串转对象
		this.Signboard = data.Ident_Signboard;
		this.Signguid = data.Ident_Signguid;
		uni.request({ 
			//获取子账号信息
			url: 'http://192.168.199.194/mobile/Subaccount/getSubAccount',  //调用子账号接口的地址
			method: 'GET',
			data: {
				Ident_Signboard: this.Signboard,
				Ident_Signguid: this.Signguid
			},
			success: (res) => {
				if(res.data.status == 0){
					this.subaccountInfo = res.data.subaccount;
				}else{
					uni.showToast({
						title:'获取信息失败',
						icon:'none'
					})
				}
			}
		})
	}
}

